<template>
  <div class="personal-content-body">
    <div class="table-content">
      <div class="clear table-content-title">
        <div class="left">信息发布</div>
        <el-input placeholder="搜索" v-model="searchText" class="header-right-time right" :clearable='true' @change="initData()">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
      </div>
      <div class="table-group">
        <div class="table-group-content">
          <div class="clear table-group-title">
            <div class="left breadcrumb">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{name:'infoPublishIndex'}">信息发布</el-breadcrumb-item>
                <el-breadcrumb-item>机构公告发布历史</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="gotoCreate(-1)">
                <span class="iconfont icon-add"></span>发布新公告
              </div>
            </div>
            <div class="line"></div>
          </div>
          <!-- pe数据列表 -->
          <div class="historyapply-group-tablebox table-group-tablebox">
            <div class="table-normal-box">
              <table class="table-normal">
                <tr>
                  <th width="30">序号</th>
                  <th width="30%">文章标题</th>
                  <th>发送时间</th>
                  <th width="30%">标签</th>
                  <th>操作</th>
                </tr>
                <tr v-for="(item,index) in datalist" :key="item.id">
                  <td>{{pageSize*(currentPage-1)+index+1}}</td>
                  <td class="table-ellipsis-box">{{item.title}}</td>
                  <td>{{item.add_time}}</td>
                  <td><span class="table-span" v-for="item1 in item.tag">{{item1}}</span></td>
                  <td>
                    <span class="table-link" @click="gotoCreate(item.id)">编辑</span>
                    <span class="table-link" @click="gotoDelete(item.id)">删除</span>
                  </td>
                </tr>
              </table>
              <div class="table-nodata" v-if="total==0">
                <img src="../../images/table_nodata.png"> 没有对应数据
              </div>
            </div>
            <div class="pagination-normal" v-if="total!=0">
              <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="getlistdata" :current-page="currentPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { bulletinHis, delBullHis } from '../../config/api'
import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
export default {
  data() {
    return {
      pageSize: 10,
      searchText: '', //查询数据
      datalist: [], //项目列表
      total: 10,
      currentPage: 1,
    }
  },
  created() {
    this.initData();
  },
  computed: {},
  mounted() {},
  methods: {
    initData() {
      this.currentPage = 1;
      this.getList();
    },
    //获取pe app端资讯列表
    getList() {
      bulletinHis({
        search: this.searchText,
        number: this.pageSize,
        page: this.currentPage
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.datalist = res.data.payload.data;
          this.total = res.data.payload.total;
        }
      })
    },
    getlistdata(val) {
      if (this.total > this.pageSize) {
        this.currentPage = val
        this.getList();
      }

    },
    //前往发布页面(type=>{new/pe/lp})
    gotoCreate(id) {
      this.$router.push({
        name: 'gpNoticePublish',
        query: {
          id: id
        }
      })
    },
    //删除
    gotoDelete(id) {
      dialogConfirmHasbtn('确定删除吗？').then(() => {
        delBullHis({
          id: id
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '删除成功')
          } else {
            dialogConfirm('fail', res.data.message)
          }
          this.initData();
        })
      }).catch(() => {});

    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.table-group-title-btn {
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
  }
}

.peopleSelectTitle-text {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 40px;
  margin-right: 10px;
}

.peopleSelectTitle {
  border-bottom: 1px solid #dcdfe6;
}

.table-span {
  background: #E7F4FF;
  color: #3C98E8;
  font-size: 12px;
  margin: 2px;
  padding: 0px 5px;
  line-height: 23px;
  display: inline-block;
  max-width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  float: left;
}

.table-normal {
  table-layout: fixed;
}

</style>



